import classes from './WorkingHours.module.css';

const WorkingHours = () => {
    return (
        <div className={ classes.items }>
            <p> :</p>
            <time dateTime="-: 09:00 - 21:00">-: 09:00 - 21:00</time>
            <time dateTime=": 09:00 - 18:00">: 09:00 - 18:00</time>
            <time dateTime=": ">: </time>
        </div>
    )
};

export default WorkingHours;
